.header--block {
	text-align: center;
	color: #646262;
}

.header--title {
	font-size: 40px;
}
.header--font {
	font-weight: 900;
}
.header--button {
	margin-top: 50px;
	display: block;
	margin-left: auto;
	margin-right: auto;
	width: 250px;
	height: 50px;
	background-color: #ec5483;
	border-radius: 24px;
	border: none;
	padding-top: 10px;
	font-size: 18px;
	color: #fff;
	text-transform: capitalize;
}

.header--button:hover {
	text-decoration: none;
	color: #fff;
	opacity: 0.7;
}