.block-services {
	text-align: center;
	padding-top: 70px;
	padding-bottom: 100px;
}

.services-item {
	margin-top: 30px;
}

.services-item-content {
	display: block;
	margin-left: auto;
	margin-right: auto;
	font-size: 14px;
	padding-top: 40px;
	padding-bottom: 40px;
	color: #8c8c8c;
}

.services-item-link {
	text-transform: uppercase;
	color: #615f5f;
	font-weight: 700;
}

.services-item-link:hover{
	text-decoration: none;
	color: inherit;
}

