@media screen and (max-width: 540px) {
	.block-blog,
	.block-content {
		text-align: center;
	}
}

.block-features {
	padding-top: 10px;
	padding-bottom: 50px;
}

.features-item {
	margin-top: 40px;
}

.features-item-content {
	display: block;
	margin-bottom: 20px;
	color: #8c8c8c;
	font-size: 14px;
}

.features-item-link {
	color: #615f5f;
	font-size: 13px;
	text-transform: uppercase;
	font-weight: 700;
}

.features-item-link:hover {
	text-decoration: none;
	color: inherit;
}