.block-takeaction {
	text-align: center;
	padding-top: 30px;
	padding-bottom: 50px;
}

.takeaction-title {
	display: block;
	font-size: 20px;
	color: #686868;
	margin-top: 20px;
}

.title-weigth {
	font-weight: 700;
}

.takeaction-link {
	margin-top: 50px;
	display: block;
	margin-left: auto;
	margin-right: auto;
	width: 250px;
	height: 50px;
	background-color: #ec5483;
	font-weight: 700;
	border: none;
	padding-top: 13px;
	font-size: 18px;
	color: #fff;
	text-transform: uppercase;
}

.takeaction-link:hover {
	text-decoration: none;
	color: #fff;
	opacity: 0.7;
}