.brand-block {
	text-align: center;
	padding-top: 30px;
	padding-bottom: 50px;
	color: #d6d3d3;
}

.brand-item {
	margin-top: 20px;
	border: 3px solid #d6d3d3;
	padding-top: 20px;
	padding-bottom: 20px;
	padding-left: 10px;
	padding-right: 10px;
}

.brand-title {
	font-weight: 700;
	font-size: 20px;
	text-transform: uppercase;
	font-style: italic;
}

.brand-content {
	display: block;
	margin-left: auto;
	margin-right: auto;
	margin-top: -4px;
	font-size: 8px;
}